<template>
    <div class="main-container">
        <div v-for="Image in ImageList" :key="Image" class="image-container overlay" >
            <img :src="resolve_img_url(Image)"  class="image" />
        </div>
    </div>
</template>

<script>
    export default {
        name: 'RenderView' ,
        props : {
            ImageList: Array ,
        },
        methods: {
            resolve_img_url: function (path) {
                let images = require.context('../ImageList/', false, /\.svg$|\.png$/)
                return images("./"+path)
            }   
        }
    }
</script>

